@font-face{font-family:"Roboto";src:url(assets/fonts/Roboto-Regular.ttf) format("truetype");font-weight:normal;font-style:normal}body{cursor:url("https://img.icons8.com/doodle/48/000000/fantasy.png"),auto;background-image:url(assets/images/crystal-ball.jpg);background-size:100%;background-color:black;background-repeat:no-repeat;font-family:"Roboto";color:#fff}body h1{text-align:center;padding-top:20px}body p{text-align:center}body .form{text-align:center}body .form input{width:90%;padding:40px 10px;margin:20px;background-color:rgba(27,25,25,0.5);color:#679cda}body .form #loader1{display:none;margin-top:30px}body .form .loader{display:none;margin:0 auto;border:16px solid #f3f3f3;border-top:16px solid #3498db;border-radius:50%;width:120px;height:120px;animation:spin 2s linear infinite}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}body #phrase{padding-top:50px;font-size:26px}body .loaderEnCours{padding-top:50px}@media all and (max-device-width: 480px){body{background-color:black}}@media (min-device-width: 480px) and (max-device-width: 1024px){body{background-color:black}}
